<template>
    <div class="col-10 col-sm-8 col-md-6 col-lg-4 mx-auto my-3">
        <div v-if="errorMsg != ''" class="bg-danger text-white p-3 mb-3">
            {{ errorMsg }}
        </div>
        <form @submit.prevent="loginAction">
            <h3 class="text-center">ログイン</h3>
            <div class="mb-3">
                <label for="exampleInputEmail1" class="form-label">メールアドレス<small class="text-danger">*</small></label>
                <div class="input-group mb-3">
                    <div class="input-group-text">
                        <i class="bi bi-envelope-at-fill"></i>
                    </div>
                    <input v-model="email" type="email" class="form-control" id="exampleInputEmail1"
                        aria-describedby="emailHelp" required>
                </div>
            </div>

            <div class="mb-3">
                <label for="exampleInputPassword1" class="form-label">パスワード<small class="text-danger">*</small></label>
                <div class="input-group mb-3">
                    <input v-model="password" :type="pwdShowHide ? 'password' : 'text'" class="form-control"
                        id="exampleInputPassword1" required>
                    <div class="input-group-text" @click="pwdShowHideAction">
                        <i class="bi" :class="pwdShowHide ? 'bi-eye-fill' : 'bi-eye-slash-fill'"></i>
                        <!-- <i class="bi bi-eye-slash-fill"></i> -->
                    </div>
                </div>
            </div>

            <div class="mb-3">
                <input type="submit" class="btn btn-primary full-width" value="ログイン">
            </div>
        </form>
        <div class="text-center">
            <router-link :to="{ name: 'renew-pass' }">パスワードを忘れた場合</router-link>
        </div>

        <hr class="mb-3">

        <div class="mb-3">
            <router-link :to="{ name: 'create-acccount' }" class="btn btn-success full-width">新しいアカウントを作成</router-link>
        </div>
    </div>

    <action-alert ref="alertComp" :alertData="alertData" @actionCallBack="resendURL"></action-alert>
    <no-action-alert ref="noAlertComp" :alertData="alertData"></no-action-alert>
</template>
<script>
import { firstCharCap } from '@/assets/js/StringFormat';
import { loadLocalStorage } from '@/bstorage';
import ActionAlert from '@/components/ActionAlert.vue';
import NoActionAlert from '@/components/NoActionAlert.vue';

let modal;
export default {
    data() {
        return {
            'email': '',
            'password': '',
            'errorMsg': '',
            'pwdShowHide': true,
            alertData: {},
            'userCred': null
        }
    },
    components: {
        'action-alert': ActionAlert,
        'no-action-alert': NoActionAlert,
    },
    methods: {
        loginAction() {
            this.$store.dispatch('loginAction',
                {
                    'email': this.email,
                    'password': this.password
                }
            )
                .then((response) => {
                    this.userCred = response.user
                    this.alertData.type = response.myStatus
                    if (response.code && response.code.split('/').length > 1) {
                        this.alertData.title = firstCharCap(response.code.split('/')[0], ' ')
                        this.alertData.content = firstCharCap(response.code.split('/')[1], '-')
                    } else {
                        this.alertData.title = 'Info'
                        this.alertData.content = response.extraMsg
                    }

                    if (modal != null) {
                        modal.hide()
                        modal = null
                    }

                    if (response.rebounce) {
                        modal = new bootstrap.Modal(this.$refs.alertComp.$refs.actionAlert, {})
                    } else {
                        modal = new bootstrap.Modal(this.$refs.noAlertComp.$refs.noActionAlert, {})
                    }
                    modal.show()
                })
        },
        pwdShowHideAction() {
            this.pwdShowHide = !this.pwdShowHide
        },
        resendURL() {
            this.$store.dispatch('sendEmailVerification', this.userCred)
                .then((response) => {
                    this.alertData.type = response.myStatus
                    if (response.code && response.code.split('/').length > 1) {
                        this.alertData.title = firstCharCap(response.code.split('/')[0], ' ')
                        this.alertData.content = firstCharCap(response.code.split('/')[1], '-')
                    } else {
                        this.alertData.title = 'Info'
                        this.alertData.content = response.extraMsg
                    }
                    // hide previoud modal
                    if (modal != null) {
                        modal.hide()
                    }

                    // recreate new modal
                    modal = new bootstrap.Modal(this.$refs.noAlertComp.$refs.noActionAlert, {})
                    modal.show()
                })
        }
    },
    created() {
        // After user verify/click email-link
        // email is save in local storage(session)
        this.email = loadLocalStorage('email')
    }
}
</script>
<style scoped></style>